@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600&family=Poppins:wght@200;300;400;500;600&display=swap');
/* font-family: 'Poppins', sans-serif; */
*{
  margin: 0;
  padding: 0;
  box-sizing:border-box;
  scrollbar-width: thin;
}


a{
  text-decoration: none;
  color: inherit;
}

.app{
  min-height: 100vh;
  width: 100%;
  border: 8px solid #28706a93;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header{
  color: #fff;
  text-decoration: none;
  font-size: 3rem;
  text-align: center;
  display: block;
  font-weight: 500;
  border-bottom: 5px solid #fff;
  font-family: 'Montserrat', sans-serif;
}

.header:hover{
  color: #fff;
}


.footer{
  color:#fff;
  width: 100%;
  background-color: #000;
  font-weight: 400;
  font-size: 1.3rem;
  text-align: center;
  padding: 1rem;
}

.footer a{
  color: #fff;
  text-decoration: none;
}

.footer a:hover{
  color: red;
}

.form{
  width: 100%;
}

.form h1{
  color: #fff;
}

.image img{
  width: 100%;
}


.quiz-form{
  background-color: transparent;
  color: #fff;
  border: 2px solid #fff;
  width: 100%;
  padding: 0.8rem;
  outline: none;
}

.quiz-form::placeholder{
  color: #fff;
}

.quiz-button{
  width: 100%;
  padding: 0.8rem;
  background-color: #fff;
  color: #000;
  border: 2px solid #fff;
  font-size: 1.3rem;
  font-weight: 500;
  outline: none;
}

.quiz-button:hover{
  background-color: transparent;
  color: #fff;
}

option{
  color: #000;
}


.quiz-header{
  color: #fff;
  text-align: right;
  background-color: transparent;
  margin-top: 1rem;
  margin-right: 3rem;
  font-size: 1.8rem;
  border: 2px solid #fff;
  padding: 0.5rem 1.5rem;
  box-shadow: 2px 3px 3px rgb(241, 238, 238);
}

.button-wrapper{
  text-align: center;
}

.optionContainer{
  border: 5px solid #fff;
}

.question-container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.option{
  border: 2px solid #fff;
  color: #fff;
  background-color: transparent;
  text-align: center;
  min-width: 200px;
  width: 550px;
}

.button-container{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.next{
  background-color: rgb(121, 192, 15);
  color: #fff;
}

.quit{
  color: #fff;
  background-color: red;
}

.bttn{
  width: 150px;
  padding: 0.3rem 1.6rem;
  font-size: 1.5rem;
  border: 0px;
  outline: none;
}

@media screen and (max-width: 991px) {
  .form{
    order: 2;
    margin-bottom: 1rem;
  }
  .image{
    order: 1;
    margin-bottom: 0.5rem;
  }
}


.select {
  background-color: rgb(7, 207, 0);
  color: white;
  box-shadow: 0 0 1px black;
}

.wrong {
  background-color: rgb(233, 0, 0);
  color: white;
  box-shadow: 0 0 1px black;
}
